import { isUndefined } from 'lodash';

import type { WizardProps } from './Wizard';
import { getWizardFooterButtons } from './WizardFooter';
import { WizardStepsContent } from './WizardStepsContent';
import { useWizardCurrentStep } from './useWizardCurrentStep';
import { Modal } from '..';
import type { ModalProps } from '..';

export interface WizardModalProps extends Omit<ModalProps, 'footer' | 'onCancel' | 'onOk'>, Omit<WizardProps, 'width'> {
  onModalClose: () => void;
}

export function WizardModal({
  onStepChanged,
  onCancel,
  initialStep,
  steps,
  onModalClose,
  localizeStepNumber,
  cancelButtonContent,
  nextButtonContent,
  previousButtonContent,
  doneButtonContent,
  ...modalProps
}: WizardModalProps) {
  const { onStepsChange, currentStepIndex, isLastStep, ...footerActions } = useWizardCurrentStep({
    initialStep,
    totalSteps: steps.length,
    onStepChanged,
  });

  if (steps.length === 0 || (!isUndefined(initialStep) && (initialStep < 0 || initialStep >= steps.length))) {
    return null;
  }

  const footerButtons = getWizardFooterButtons({
    onCancel,
    isLastStep,
    currentStepIndex,
    doneButtonContent,
    previousButtonContent,
    nextButtonContent,
    cancelButtonContent,
    ...footerActions,
    ...steps[currentStepIndex],
  });

  return (
    <Modal {...modalProps} onCancel={onModalClose} size="wide" footer={footerButtons}>
      <WizardStepsContent
        steps={steps}
        currentStepIndex={currentStepIndex}
        localizeStepNumber={localizeStepNumber}
        expandContentToFullHeight={false}
      />
    </Modal>
  );
}
