import { pick } from 'lodash';
import { useMemo } from 'react';

import type { WizardStep } from './WizardStep';
import { getShadowScrollStyles, useDesignSystemTheme } from '..';
import type { StepperProps } from '../../development/Stepper';
import { Stepper } from '../../development/Stepper';

interface WizardStepsContentProps {
  steps: WizardStep[];
  currentStepIndex: number;
  expandContentToFullHeight: boolean;
  localizeStepNumber: StepperProps['localizeStepNumber'];
}

export function WizardStepsContent({
  steps: wizardSteps,
  currentStepIndex,
  expandContentToFullHeight,
  localizeStepNumber,
}: WizardStepsContentProps) {
  const { theme } = useDesignSystemTheme();
  const stepperSteps = useMemo(
    () => wizardSteps.map((wizardStep) => pick(wizardStep, ['title', 'description', 'status'])),
    [wizardSteps],
  );

  return (
    <>
      <Stepper
        currentStepIndex={currentStepIndex}
        direction="horizontal"
        localizeStepNumber={localizeStepNumber}
        steps={stepperSteps}
        responsive={false}
      />
      <div
        css={{
          marginTop: theme.spacing.md,
          flexGrow: expandContentToFullHeight ? 1 : undefined,
          overflowY: 'auto',
          ...getShadowScrollStyles(theme),
        }}
      >
        {wizardSteps[currentStepIndex].content}
      </div>
    </>
  );
}
